import { mail, lock, phonePortrait, person, people } from 'ionicons/icons';
import React, { useState } from 'react';
import axios from 'axios';
import {
  IonButton,
  IonContent,
  IonIcon,
  IonRow,
  IonGrid,
  IonItem,
  IonList,
  IonInput,
  IonLabel
} from '@ionic/react';
import './MyAccount.css';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import Analytics from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet';

interface myAccountProps {
  firstName?: string;
  lastName?: string;
  email?: string;
  phone?: string;
  role?: string;
  title: string;
  route: string;
}

const MyAccount: React.FunctionComponent<myAccountProps> = props => {
  const [member, setMember] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    password: '',
    role: ''
  });

  const signUp = async (member: any) => {
    const axiosHeaders = {
      'Content-Type': 'application/x-www-form-urlencoded',
      'x-api-key': `${process.env.REACT_APP_API_PUB_KEY}`
    };
    axios.request({
      method: 'POST',
      url: '/user',
      baseURL: process.env.REACT_APP_API_URL,
      //   data: '',
      headers: axiosHeaders,
      params: member
    });
  };

  return (
    <>
      <SlyHelmet {...props} />
      <Analytics pageName='account' />
      <Header
        pageTitle={`${member.firstName} ${member.lastName}`}
        hideLogin={true}
        hideSignUp={true}
      />
      <IonContent>
        <IonButton onClick={e => signUp(member)}>test</IonButton>
      </IonContent>
      <Footer />
    </>
  );
};

export default MyAccount;
