import {
  IonButton,
  IonButtons,
  IonContent,
  IonHeader,
  IonIcon,
  IonMenuButton,
  IonTitle,
  IonToolbar,
  IonSlides,
  IonSlide,
  IonRow,
  IonCol,
  IonGrid,
  IonText,
  IonItem,
  IonItemGroup,
  IonFooter,
  IonFab,
  IonFabButton
} from '@ionic/react';

import { download, add, lock } from 'ionicons/icons';
import React, { useState } from 'react';
import { Nav } from './Home.json';
import './Home.css';
import '../../components/Header/Header.css';
import BottomSection from '../../components/Footer/Footer';
import Analytics, { ReactGA } from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet/';
import Footer from '../../components/Footer/Footer';

interface homeProps {
  title: string;
  route: string;
}

// Optional parameters to pass to the swiper instance. See http://idangero.us/swiper/api/ for valid options.
const slideOpts = {
  initialSlide: 0,
  speed: 4000,
  loop: true,
  autoplay: {
    delay: 3500,
    disableOnInteraction: false
  },
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  }
};

const HomePage: React.FunctionComponent<homeProps> = props => {
  const [hideClass, setHideClass] = useState('');

  const hideBar = (event: any) => {
    event.deltaY > 0
      ? !hideClass && setHideClass('navbar-hide !important')
      : hideClass && setHideClass('');
  };
  return (
    <>
      <SlyHelmet {...props} />
      <Analytics pageName='home' />
      <IonHeader translucent className={`navbar ${hideClass}`}>
        <IonToolbar className='toolbar'>
          <IonButtons slot='start'>
            <IonMenuButton />
          </IonButtons>
          <IonTitle
            className='ion-hide-md-down animated fadeIn'
            onClick={() => window.open('/#/home', '_self')}
          >
            Sly Guard
            <IonText color='medium'>
              <p
                className='animated zoomIn'
                style={{ fontSize: '.7em', margin: 0 }}
              >
                Protection Services
              </p>
            </IonText>
          </IonTitle>
          <IonTitle className='ion-hide-md-up animated fadeIn toolbar-title'>
            Sly Guard
          </IonTitle>
          <IonButtons slot='end' className='ion-hide-md-down'>
            <IonButton>Guards</IonButton>
            <IonCol size='1' slot='end'></IonCol>
            <IonButton>Clients</IonButton>
            {/* <IonButton href={'#/login'}>Login</IonButton> */}
          </IonButtons>
          <IonCol size='1' slot='end' className='ion-hide-md-down'></IonCol>
        </IonToolbar>
        <IonFab vertical='top' horizontal='end' slot='fixed'>
          <IonFabButton href={'#/login'}>
            <IonIcon icon={lock} />
          </IonFabButton>
        </IonFab>
      </IonHeader>
      <IonContent
        fullscreen
        onWheel={event => {
          hideBar(event);
        }}
        className='content-hero'
      >
        <IonSlides
          pager={true}
          options={slideOpts}
          className='intro-slides'
          style={{
            backgroundImage: `url(${process.env.PUBLIC_URL}/assets/men-in-black.jpg)`
          }}
        >
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Sly Guard</h1>
              <h4 className='intro-slides-text'>Protection Services</h4>
            </div>
          </IonSlide>
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Executive Protection</h1>
            </div>
          </IonSlide>
          <IonSlide>
            <div className='intro-slides-text-box'>
              <h1 className='intro-slides-text'>Uniformed Agents</h1>
              <IonButton
                onClick={() =>
                  ReactGA.event({
                    category: 'User',
                    action: 'sign up from hero slides'
                  })
                }
                href='#/signup'
                fill='outline'
                color='dark'
              >
                <IonIcon slot='start' icon={download} />
                Get Started
              </IonButton>
            </div>
          </IonSlide>
        </IonSlides>
        <IonGrid className='ion-hide-md-down ion-no-padding ion-align-items-center'>
          <IonRow
            className='ion-align-items-center'
            style={{ backgroundColor: 'black' }}
          >
            <IonCol className='ion-hide-md-up'></IonCol>
            <IonCol size='2' className='ion-hide-md-down'></IonCol>
            <IonCol className='ion-hide-md-down'>
              <IonButton
                href='#/signup?guard'
                color='light'
                fill='outline'
                class='ion-text-uppercase'
              >
                Apply to Guard
              </IonButton>
            </IonCol>

            <IonCol className='ion-hide-md-down'>
              <IonText color='light'>
                <p>Say hello to on-demand protection services.</p>
              </IonText>
            </IonCol>
            <IonCol className='ion-hide-md-up'></IonCol>
          </IonRow>
          <IonRow className='footer ion-hide-md-down'>
            <IonCol size='1'></IonCol>

            {Nav.map((NavItem, index) => {
              return (
                <IonCol key={index}>
                  <IonItemGroup>
                    <IonTitle className='ion-padding-bottom'>
                      {NavItem.header}
                    </IonTitle>
                    {NavItem.links.map((link, index) => {
                      return (
                        <IonItem key={index}>
                          <a href={link.url} className='link-glow'>
                            {link.title}
                          </a>
                        </IonItem>
                      );
                    })}
                  </IonItemGroup>
                </IonCol>
              );
            })}

            <IonCol></IonCol>
            <IonCol size='1'></IonCol>
          </IonRow>
        </IonGrid>
        <Footer />
      </IonContent>
    </>
  );
};

export default HomePage;
