import React from 'react';
import { Helmet } from 'react-helmet';
interface helmetProps {
  title: string;
  articleTitle?: string;
  twitterSummary?: string;
  description?: string;
  keywords?: string;
  route: string;
  noFollow?: boolean;
}

const SlyHelmet: React.FunctionComponent<helmetProps> = props => {
  return (
    <>
      <Helmet>
        <title>{props.title}</title>
        {props.description && (
          <meta name='description' content={props.description} />
        )}
        {props.keywords && <meta name='keywords' content={props.keywords} />}
        {props.articleTitle && (
          <meta
            name='og:title'
            property='og:title'
            content={props.articleTitle}
          ></meta>
        )}
        {props.twitterSummary && (
          <meta name='twitter:card' content={props.twitterSummary} />
        )}
        {props.noFollow && <meta name='robots' content='noindex, nofollow' />}
        <link rel='canonical' href={`http://slyguard.com/#${props.route}`} />
      </Helmet>
    </>
  );
};

export default SlyHelmet;
