import { mail, lock, phonePortrait, person, people } from 'ionicons/icons';
import React, { useState, useRef, useEffect } from 'react';
import {
  IonButton,
  IonContent,
  IonIcon,
  IonRow,
  IonGrid,
  IonItem,
  IonList,
  IonInput,
  IonLabel,
  IonToast,
  IonRadioGroup,
  IonListHeader,
  IonRadio,
  IonModal,
  IonItemGroup
} from '@ionic/react';
import './SignUp.css';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import axios from 'axios';
import {
  CognitoUserPool,
  CognitoUserAttribute,
  CognitoUser
} from 'amazon-cognito-identity-js';
import Analytics from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet';

interface signUpProps {
  firstName?: string;
  lastName?: string;
  email?: string;
  phone?: string;
  password?: string;
  role?: string;
  title: string;
  route: string;
  id?: string;
}

const SignUp: React.FunctionComponent<signUpProps> = props => {
  const [member, setMember] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    password: '',
    role: ''
  });
  const [toast, setToast] = useState({
    show: false,
    data: ''
  });
  const [isFormInvalid, setIsFormInvalid] = useState(true);
  const [showModal, setShowModal] = useState(false);

  const firstNameRef: any = useRef(null);
  const lastNameRef: any = useRef(null);
  const emailRef: any = useRef(null);
  const phoneRef: any = useRef(null);
  const passwordRef: any = useRef(null);
  const roleRef: any = useRef(null);

  const canBeSubmitted = () => {
    firstNameRef.current.value.length > 0 &&
    lastNameRef.current.value.length > 0 &&
    emailRef.current.value.length > 0 &&
    phoneRef.current.value.length === 10 &&
    passwordRef.current.value.length >= 8
      ? setIsFormInvalid(false)
      : setIsFormInvalid(true);
    if (isFormInvalid == true)
      setMember({
        ...member,
        firstName: firstNameRef.current.value,
        lastName: lastNameRef.current.value,
        email: emailRef.current.value,
        phone: phoneRef.current.value,
        password: passwordRef.current.value,
        role: roleRef.current.value || 'client'
      });
  };

  const createAccount = async (member: any) => {
    const axiosHeaders = {
      'Content-Type': 'application/x-www-form-urlencoded',
      'x-api-key': `${process.env.REACT_APP_API_PUB_KEY}`
    };
    axios.request({
      method: 'POST',
      url: '/user',
      baseURL: process.env.REACT_APP_API_URL,
      //   data: '',
      headers: axiosHeaders,
      params: member
    });
  };

  const [formErrors, setFormErrors] = useState({ message: '' });
  const signUp = (member: any) => {
    const poolData = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };

    const userPool = new CognitoUserPool(poolData);

    const attributeList = [];

    const dataEmail = {
      Name: 'email',
      Value: member.email
    };

    const dataPhoneNumber = {
      Name: 'phone_number',
      Value: '+1' + member.phone
    };
    const dataFirstName = {
      Name: 'given_name',
      Value: member.firstName
    };
    const dataLastName = {
      Name: 'family_name',
      Value: member.lastName
    };
    const dataRole = {
      Name: 'custom:role',
      Value: member.role
    };

    const attributeEmail = new CognitoUserAttribute(dataEmail);
    const attributePhoneNumber = new CognitoUserAttribute(dataPhoneNumber);
    const attributeFirstName = new CognitoUserAttribute(dataFirstName);
    const attributeLastName = new CognitoUserAttribute(dataLastName);
    const attributeRole = new CognitoUserAttribute(dataRole);

    attributeList.push(attributeEmail);
    attributeList.push(attributePhoneNumber);
    attributeList.push(attributeFirstName);
    attributeList.push(attributeLastName);
    attributeList.push(attributeRole);

    userPool.signUp(
      member.email,
      member.password,
      attributeList,
      [],
      (err: any, result: any) => {
        if (err) {
          setToast({
            ...toast,
            show: true,
            data: err.message || JSON.stringify(err)
          });
          // alert(err.message || JSON.stringify(err));
          return;
        }
        const cognitoUser = result.user;

        window.open('#/confirmation?' + member.email, '_self');

        // if (result.user != '') setShowModal(true);

        // setMember({ ...member, id: result.user });
        // createAccount(member);

        console.log('user name is ' + cognitoUser.getUsername());
      }
    );

    console.log('cognito done' + JSON.stringify(member));
  };

  const submit = async () => {
    console.log(JSON.stringify(member));

    try {
      await signUp(member);
    } catch (e) {
      setFormErrors(e);
    }
  };
  useEffect(() => {
    firstNameRef.current.focus();
  });
  return (
    <>
      <SlyHelmet {...props} />
      <Analytics pageName={props.route} />
      <Header pageTitle={'Sign Up'} hideLogin={false} hideSignUp={true} />
      <IonContent>
        <IonModal isOpen={showModal}>
          <p>Account Created</p>
          <IonButton
            href='#/home'
            // onClick={() => setShowModal(false)}
          >
            Close
          </IonButton>
        </IonModal>
        <IonGrid>
          <IonRow className='ion-justify-content-center'>
            <form
              style={{ width: '300px' }}
              onSubmit={e => {
                e.preventDefault();
                submit();
              }}
            >
              <div>{formErrors ? formErrors.message : null}</div>
              <IonList>
                <IonItem onClick={() => firstNameRef.current.focus()}>
                  <IonLabel color='medium' position='floating'>
                    First name
                  </IonLabel>
                  <IonInput
                    ref={firstNameRef}
                    // autofocus={true}
                    required
                    name='firstName'
                    type='text'
                    // value={}
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      className='input-icon'
                      color='medium'
                      icon={person}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem onClick={() => lastNameRef.current.focus()}>
                  <IonLabel color='medium' position='floating'>
                    Last name
                  </IonLabel>
                  <IonInput
                    ref={lastNameRef}
                    required
                    name='lastName'
                    type='text'
                    // value={member.lastName}
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      className='input-icon'
                      color='medium'
                      icon={people}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem onClick={() => phoneRef.current.focus()}>
                  <IonLabel color='medium' position='floating'>
                    Mobile Phone
                  </IonLabel>
                  <IonInput
                    ref={phoneRef}
                    required
                    min-length={10}
                    max-lenght={10}
                    name='phone'
                    type='tel'
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      className='input-icon'
                      color='medium'
                      icon={phonePortrait}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem onClick={() => emailRef.current.focus()}>
                  <IonLabel color='medium' position='floating'>
                    Email
                  </IonLabel>
                  <IonInput
                    ref={emailRef}
                    required
                    name='email'
                    type='email'
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      className='input-icon'
                      color='medium'
                      icon={mail}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem onClick={() => passwordRef.current.focus()}>
                  <IonLabel color='medium' position='floating'>
                    Password
                  </IonLabel>
                  <IonInput
                    ref={passwordRef}
                    required
                    name='password'
                    type='password'
                    min='8'
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      className='input-icon'
                      color='medium'
                      icon={lock}
                    />
                  </IonInput>
                  {/* <p>Number</p>
                  <p>Special Character</p>
                  <p>Uppercase Character</p>
                  <p>Lowercase Letter</p> */}
                </IonItem>
                <br />

                <IonItem>
                  <IonRadioGroup style={{ width: '100%' }} ref={roleRef}>
                    <IonListHeader>Signup as:</IonListHeader>
                    <IonItem slot='start'>
                      <IonLabel>Guard</IonLabel>
                      <IonRadio value='guard' />
                    </IonItem>
                    <IonItem slot='end'>
                      <IonLabel>Client</IonLabel>
                      <IonRadio value='client' />
                    </IonItem>
                  </IonRadioGroup>
                </IonItem>
                <br />
              </IonList>

              <IonButton
                style={{ width: '100%' }}
                type='submit'
                disabled={isFormInvalid}
                onClick={() => setIsFormInvalid(true)}
              >
                Register
              </IonButton>
            </form>
          </IonRow>
        </IonGrid>
      </IonContent>
      <Footer />
      <IonToast
        isOpen={toast.show}
        onDidDismiss={() => setToast({ ...toast, data: '', show: false })}
        message={toast.data}
        position='top'
        // duration={700}
        buttons={[
          // {
          //   side: 'start',
          //   icon: 'star',
          //   text: 'Favorite',
          //   handler: () => {
          //     console.log('Favorite clicked');
          //   }
          // },
          {
            text: 'Done',
            role: 'cancel',
            handler: () => {
              // console.log('Cancel clicked');
            }
          }
        ]}
      />
    </>
  );
};

export default SignUp;
