import {
  IonContent,
  IonHeader,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonMenu,
  IonMenuToggle,
  IonToolbar,
  IonAvatar,
  IonButton,
  IonText,
  IonTitle,
  IonCol,
  IonRow,
  IonGrid
} from '@ionic/react';
import { mail, logoInstagram, logoTwitter } from 'ionicons/icons';
import React from 'react';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { AppPage } from '../../declarations';

import './Menu.css';

interface MenuProps extends RouteComponentProps {
  appPages: AppPage[];
}

const Menu: React.FunctionComponent<MenuProps> = ({ appPages }) => (
  <IonMenu contentId='main' className='ion-hide-md-up'>
    <IonHeader className='ion-text-center'>
      <IonToolbar>
        {/* <IonAvatar className='profile-photo-avatar'>
          <img
            src={`${process.env.PUBLIC_URL}/assets/profilePhoto.jpg`}
            alt='Sly Guard Logo'
          />
        </IonAvatar> */}
        <IonTitle className='animated fadeInLeft ion-hide-md-down'>
          Sly Guard
        </IonTitle>

        <h3 className='ion-hide-md-up' style={{ margin: 0 }}>
          Sly Guard
        </h3>
        <IonText color='medium'>
          <p
            className='animated zoomIn'
            style={{ fontSize: '.7em', margin: 0, paddingBottom: '.5em' }}
          >
            Protection Services
          </p>
        </IonText>
      </IonToolbar>
    </IonHeader>
    <IonContent className='ion-text-center'>
      <IonList>
        {appPages.map((appPage, index) => {
          if (!appPage.noSideMenu)
            return (
              <IonMenuToggle
                className='ion-text-center'
                key={index}
                autoHide={false}
              >
                <IonItem href={'#' + appPage.url}>
                  <IonIcon slot='start' icon={appPage.icon} />
                  <IonLabel>{appPage.title}</IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
        })}
      </IonList>
      <IonGrid className='menu-grid'>
        <IonRow className='ion-align-items-center'>
          <IonCol>
            <IonButton
              onClick={() =>
                window.open('https://twitter.com/slyguard', '_system')
              }
              fill='clear'
              color='medium'
            >
              <IonIcon slot='icon-only' icon={logoTwitter} />
            </IonButton>
          </IonCol>
          <IonCol>
            <IonButton
              onClick={() =>
                window.open('https://www.instagram.com/slyguard', '_system')
              }
              fill='clear'
              color='medium'
            >
              <IonIcon slot='icon-only' icon={logoInstagram} />
            </IonButton>
          </IonCol>
          <IonCol>
            <IonButton
              onClick={() =>
                window.open('mailto:slyguard.com@gmail.com', '_self')
              }
              fill='clear'
              color='medium'
            >
              <IonIcon slot='icon-only' icon={mail} />
            </IonButton>
          </IonCol>
        </IonRow>
      </IonGrid>
    </IonContent>
  </IonMenu>
);

export default withRouter(Menu);
